.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 5px 20px;
	background: linear-gradient(45deg, #0d0d0d, #434343);
}

.header .name {
	font-size: 1.2rem;
	font-weight: bold;
	color: #ffffff;
}

.header .tabs {
	display: flex;
	gap: 20px;
}

.header .tab {
	padding: 5px 15px;
	font-size: 1rem;
	font-weight: bold;
	color: #ffffff;
	background: #333;
	border-radius: 3px;
	cursor: pointer;
	transition: background 0.3s ease, transform 0.3s ease;
}

.header .tab:hover {
	background: #555;
	transform: scale(1.05);
}

.header .tab.active {
	background: #00ccff;
	color: #000;
	transform: scale(1.1);
}

.header .links {
	display: flex;
	gap: 15px;
}

.header .links a {
	text-decoration: none;
	color: #ffffff;
	font-size: 1.2rem;
	transition: transform 0.3s ease, color 0.3s ease;
}

.header .links a:hover {
	color: #00ccff;
	transform: scale(1.2) rotate(360deg);
}
